import React from 'react';
import { HashRouter, BrowserRouter, Switch, Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import MainBill from './components/MainBill';
import Orders from './components/Orders'
import Button from './components/test'
import Cheese from './components/Cheese'
import Dairy from './components/Dairy'
import Hortalizas from './components/Hortalizas'
import Eggs from './components/Eggs'
import Vision from './pages/Vision'
import Contact from './pages/Contact'

export default function App() {
    return (
      <HashRouter basename='/'>
          <BrowserRouter>
            <Switch>
              <Route exact path="/" component={MainBill} />
              <Route exact path="/test" component={Button} />
              <Route exact path="/orders" component={Orders} />
              <Route exact path="/cheese" component={Cheese} />
              <Route exact path="/dairy" component={Dairy} />
              <Route exact path="/hortalizas" component={Hortalizas} />
              <Route exact path="/eggs" component={Eggs} />
              <Route exact path="/vision" component={Vision} />
              <Route exact path="/contacto" component={Contact} />
            </Switch>
          </BrowserRouter>
      </HashRouter>
    );
  }
  
